.Pagination {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  background-color: var(--primary-color);
}

.Pagination-link {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  display: block;
  cursor: pointer;
  padding: 30px;
  text-align: center;
  color: rgba(0, 0, 0, 0.5);
  font-size: 14px;
  text-transform: uppercase;
  font-weight: bold;
  -webkit-transition: -webkit-box-shadow 0.2s;
  transition: -webkit-box-shadow 0.2s;
  -o-transition: box-shadow 0.2s;
  transition: box-shadow 0.2s;
  transition: box-shadow 0.2s, -webkit-box-shadow 0.2s;
}

.Pagination-link:hover {
  -webkit-box-shadow: inset 0 0 100rem rgba(0, 0, 0, 0.1);
          box-shadow: inset 0 0 100rem rgba(0, 0, 0, 0.1);
}

.Pagination-info {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  display: block;
  padding: 30px;
  text-align: center;
  color: rgba(0, 0, 0, 0.5);
  font-size: 14px;
  text-transform: uppercase;
  border-style: solid;
  border-width: 0 1px;
  border-color: rgba(0, 0, 0, 0.2);
}



